@import url(https://fonts.googleapis.com/css2?family=Urbanist&display=swap);
#root {
    width: 100%;
    height: 100%;
    max-width: 1440px;

  }
  
  * {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    font-family: 'Urbanist', sans-serif;
    }
  body, html {    
    display: flex;
    flex-direction: row;
    align-content: center;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 100%;
    font-family: 'Urbanist', sans-serif;
  }
  .App {
    width: 100%;
    height: 100%;
  }

a {
    text-decoration: none;
    color: black;
    font-size: 1.2rem;
}


.form-filters {
    width: 100%;
    
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;

    margin: 20px 0;   
}

@media (max-width: 600px) {
  .form-filters {
    width: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    margin: 20px 0;
    justify-content: center;
  }
  .form-filters *:not(span, :last-child) {
    margin-bottom: 20px;
    margin-right: 0px;

  }
}

.form-filters *:not(span, :last-child) {
    margin-right: 20px;
}


footer {    
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;

  background-color: rgba( 3,37,65);
  width: 100%;
  align-content: center;

  font-size: 1.2rem;
}
.footer-wrapper {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;

  margin: 30px;

  color: white;
}
.Navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    align-content: center;
    flex-wrap: wrap;

    padding: 20px;
}
.nav-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    align-content: center;
    flex-wrap: wrap;

    list-style: none;
}
.nav-wrapper li {
    margin-right: 20px;
    text-decoration: none;
}
.nav-wrapper li:last-child {
    margin-right: 0px;
}
.selected {
    color: blue !important;
}

@media (max-width: 600px) {
    .Navbar {
        display: flex;
        justify-content: center;
        align-content: center;
        flex-wrap: wrap;
        padding: 20px;
        flex-direction: column;
        align-items: center;
    }
    .nav-wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
        list-style: none;
        flex-direction: column;
        align-content: center;
    }
    .nav-wrapper li {
        margin-right: 0px;
    }
  }
.Characters {
    padding: 20px;

    width: 100%;
    min-height: 100%;
}

.character-filters {
    margin: 20px 0;
}

.character-wrapper {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
    align-content: center;
    flex-direction: row;
}

.character-item {
    position: relative;

    width: 300px;
    height: 300px;
    margin-bottom: 40px;

    display: flex;
    flex-direction: column;
    justify-content: space-between;

    overflow: hidden;

    cursor: pointer;
}

.character-item-status {
    text-align: end;
    margin: 10px;
}
.character-item-status span {
    background-color: rgba(9, 67, 255, 0.501);
    padding: 5px 10px;
    border-radius: 10px ;
}

.line-middle {
    border: 15px solid black;
    width: 85px;
    height: 0px;
    position: absolute;
    bottom: 0px;
    right: -20px;
    -webkit-transform: rotate(-30deg);
    transform: rotate(-40deg);
  }

  .character-item-name {
    background-color: rgba(255, 255, 255, 0.707);
    padding: 10px;
  }
.CharacterDetails {
    position: fixed;
    width: 300px;
    height: 300px;

    /* margin: 15px; */

    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    box-shadow: 0px 0px 10px 10px rgba(34, 60, 80, 0.16);
}

.CharacterDetails-box {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: flex-end;
    align-content: center;
    flex-wrap: wrap;

    border-radius: 10px;
    padding: 15px;

    width: 100%;
    height: 100%;
    background-color: rgba(34, 60, 80, 0.59);

    color: rgb(255, 255, 255);
    font-size: 1.2rem;
}



.Episodes {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;

    padding: 20px;

    width: 100%;
    min-height: 100%;
}

.Episodes-wrapper {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    align-content: center;
    justify-content: space-around;
    align-items: center;    
}

.Episodes-item {
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
    align-items: center;
    text-align: center;
    
    width: 150px;
    height: 150px;
    margin: 10px;
    border: 1px solid #7f7f7f;
    cursor: pointer;
    border-radius: 10px;
    padding: 10px;
}
.EpisodesCharactersPhoto {
    width: 100%;
    height: 100%;

    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    justify-content: flex-end;
}
.EpisodesCharacters {
    width: 500px;

    position: fixed;
    background-color: rgba(34, 60, 80, 0.59);
    color: white;

    padding: 10px;

    border-radius: 10px;
        
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    box-shadow: 0px 0px 10px 10px rgba(34, 60, 80, 0.16);
}

.EpisodesCharacters-topBar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    align-content: center;
    flex-direction: row;
    flex-wrap: wrap;
}

.swiper-slide {
    width: unset !important;
}


.EpisodesCharacters-item{
    width: 100px;
    height: 100px;
}


.Location {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-content: flex-start;
    justify-content: center;
    
    padding: 20px;

    width: 100%;
    min-height: 100%;
}

.Location-wrapper {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.Location-item {
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
    align-items: center;
    text-align: center;
    
    width: 150px;
    height: 150px;
    margin: 10px;
    border: 1px solid #7f7f7f;
    cursor: pointer;
    border-radius: 10px;
    padding: 10px;
}



.LocationResidents {
    width: 500px;

    position: fixed;
    background-color: rgba(34, 60, 80, 0.59);
    color: white;

    padding: 10px;
        
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);

    border-radius: 10px;
    box-shadow: 0px 0px 10px 10px rgba(34, 60, 80, 0.16);
}

.LocationResidents-item{
    width: 100px;
    height: 100px;
}

.LocationResidents-topBar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    align-content: center;
    flex-direction: row;
    flex-wrap: wrap;
}
.LocationResidentsFoto-wrapper, .LocationResidentsFoto-wrapper {
    width: 100%;
    height: 100%;

    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    justify-content: flex-end;
}
.notice {
    display: flex;
    justify-content: center;
}

.MyWarchList {
    padding: 20px;

    width: 100%;
    min-height: 100%;
}

.MyWarchList-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    align-content: center;
    flex-direction: column;
    flex-wrap: wrap;
}

.MyWarchList-item {
    display: flex;
    text-decoration: none;
    align-items: center;
    justify-content: center;
    align-content: center;
    flex-wrap: wrap;
    flex-direction: row;
}

.MyWarchList-item-text {
    width: 300px;
    text-align: center;
}
.welcome {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    align-content: center;
    flex-wrap: wrap;
    flex-direction: column;
}
